<template>
  <h2 v-if="type === 'h2'" v-bind="$attrs"><slot /></h2>
  <h3 v-else-if="type === 'h3'" v-bind="$attrs"><slot /></h3>
  <h4 v-else-if="type === 'h4'" v-bind="$attrs"><slot /></h4>
  <h5 v-else-if="type === 'h5'" v-bind="$attrs"><slot /></h5>
</template>
<script>
export default {
  name: 'BaseHeading',
  props: {
    type: {
      type: String,
      default: 'h3'
    }
  }
}
</script>
