import Vue from "vue"
import App from "./App.vue"
import router from "./router"

import VueLogger from 'vuejs-console-logger' // https://github.com/hellomatter/vuejs-logger
import vClickOutside from 'v-click-outside'

import "./registerServiceWorker"


import i18n from '@i18n'
import '@components/_globals'

import VueSlideoutPanel from 'vue2-slideout-panel'

import uuidv4 from 'uuid/v4' // Generate random UUIDv4

import CustomGoogleAutocomplete from 'vue-custom-google-autocomplete'

import VueScrollTo from 'vue-scrollto'

Vue.config.productionTip = false // https://vue2-slideout-panel.wrkflows.io/

Vue.use(vClickOutside)
Vue.use(VueSlideoutPanel)
Vue.use(CustomGoogleAutocomplete)
Vue.use(VueScrollTo)

Vue.use(VueScrollTo, {
  container: "body",
  duration: 300,
  easing: "ease",
  offset: 0,
  force: true,
  cancelable: true,
  onStart: false,
  onDone: false,
  onCancel: false,
  x: false,
  y: true
})

/** Vue Logger
 * https://github.com/hellomatter/vuejs-logger
 */
const VueLoggerOptions = {
  logLevel: (process.env.NODE_ENV === 'production') ? 'error' : 'debug',
  stringifyArguments: false,
  showLogLevel: true,
  prefix: 'WeRaceKart'
}
Vue.use(VueLogger, VueLoggerOptions)

new Vue({
  router,
  i18n,
  data () {
    return {
      loggedIn: false,
      user: null
    }
  },
  methods: {
    setLocale(locale) {
      const allowedLocales = this.$i18n.availableLocales || ['en_GB']

      // If locale is not allowed, return
      if(!allowedLocales.includes(locale)) { this.$log.warn('Locale not found', locale); return false; }

      this.$i18n.locale = locale
    },
    scrollTop() {
      window.scroll({
        top: 0,
        left: 0,
        behavior: 'smooth'
      })
    },
    generateUUID() {
      return uuidv4()
    },
  },
  render: h => h(App)
}).$mount("#app")
