<script>
import HomepageSlider from '@components/homepage-slider'
import InstagramSlider from '@components/instagram-slider'
import mailgun from 'mailgun-js'

export default {
  name: 'Home',
  components: {
    HomepageSlider,
    InstagramSlider
  },
  data () {
    return {
      dates: [],
      mailgun_api_key: process.env.VUE_APP_MAILGUN_API_KEY,
      mailgun_domain: process.env.VUE_APP_MAILGUN_DOMAIN,
      mailgun_mail: process.env.VUE_APP_MAILGUN_MAIL,
      formSent: false,
      formData: {
        nome: '',
        cognome: '',
        telefono: '',
        email: '',
        data: null,
        orario: '13:30',
        numero_persone: 1,
        menu: 'null'
      }
    }
  },
  created () {
    // Get next 15 days
    var today = new Date()
    const options = { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' };

    for (let index = 0; index < 15; index++) {
      this.dates.push(new Intl.DateTimeFormat('it-IT', options).format(today.addDays(index)))
    }

    this.formData.data = this.dates[0] || null
    this.formSent = false
  },
  methods: {
    sendEmail() {
      const mg = mailgun({ apiKey: this.mailgun_api_key, domain: this.mailgun_domain, host: 'api.eu.mailgun.net' })

      const data = {
        from: `${this.formData.nome} ${this.formData.cognome} <${this.formData.email}>`,
        to: this.mailgun_mail,
        subject: 'Prenotazione',
        text: `
          Nome: ${this.formData.nome} ${this.formData.cognome}
          Telefono: ${this.formData.telefono}
          Email: ${this.formData.email}

          ---

          Data: ${this.formData.data}
          Orario: ${this.formData.orario}
          Persone: ${this.formData.numero_persone}

          ---

          Menu: ${this.formData.menu}
        `
      };

      var component = this

      mg.messages().send(data, function (error, body) { // eslint-disable-line
        // console.log(body)
        component.formSent = true
      });
    },
  }
}


Date.prototype.addDays = function(days) { // eslint-disable-line
    var date = new Date(this.valueOf());
    date.setDate(date.getDate() + days);
    return date;
}
</script>
