<template>
  <div class="input-div flex justify-start items-center relative">
    <label :for="$attrs.name" class="w-full">
      <input
        ref="input"
        v-model="newValue"
        :type="type"
        v-bind="$attrs"
        class="block w-full"
        :class="[{ 'with-icon': icon }]"
        v-on="listeners"
        spellcheck="false"
        :name="$attrs.name"
      />
      -
      <slot />
    </label>
  </div>
</template>

<script>
export default {
  name: 'BaseInput',
  props: {
    type: {
      type: String,
      default: 'text',
    },
    value: {
      type: [String, Number, Boolean],
      default: null,
    },
    icon: {
      type: [Object],
      default () {
        return null
      }
    },
  },
  data() {
    return {
      newValue: null
    }
  },
  computed: {
    listeners() {
      return {
        ...this.$listeners,
        input: event => this.$emit('input', this.newValue),
        blur: event => this.$emit('blur', event),
        change: event => this.$emit('change', event),
        focus: event => this.$emit('focus', event),
      }
    }
  },
  watch: {
    value () {
      this.newValue = this.value
    }
  },
  mounted() {
    this.newValue = this.value
    this.$root.$emit('input', event)
  }
}
</script>

<style lang="scss" scoped>
input {
  @apply border border-transparent;

  &:focus {
    @apply border border-brand;
  }

  &::placeholder {
    @apply text-gray-400;
  }
}

.with-icon {
  // padding-right: 35px!important;
}

.icon {
  @apply absolute right-0 top-0;
  right: 20px;
  top: 15px;
}

/** Homepage main inputs in hero slider */
.homepage-search input {
  @apply p-3 my-1 rounded-none;

  @screen md {
    @apply my-0;
  }
}
</style>
