<template>
  <div class="relative">
    <BaseButton class="dropdown-trigger" @click="toggleDropdown()">{{ $t('utilityBar.mobileDropdown.trigger') }}</BaseButton>
    <BaseDropdownMenu class="w-full bg-white -my-1 md:my-0 max-h-56 overflow-y-auto overflow-x-hidden" :open="showDropdown" @close="toggleDropdown(true)">
      <li v-for="n in 10"><BaseButton>{{ n }}</BaseButton></li>

    </BaseDropdownMenu>
  </div>
</template>

<script>
export default {
  data () {
    return {
      showDropdown: false
    }
  },
  methods: {
    toggleDropdown(hide = false) {
      if(hide) {
        this.showDropdown = false
      } else {
        this.showDropdown = true
      }

    }
  }
}
</script>

<style lang="scss" scoped>
a, button {
      @apply text-xs text-gray-500 leading-none;

      &:hover {
        @apply text-gray-400;
      }

      &.dropdown-trigger {
        &:after {
          content: " \f107";
          font-family: "Font Awesome 5 Pro";
          font-weight: 400;
          font-size: theme('fontSize.sm');
          vertical-align: -1px;
        }

        &:hover {
          &:after {
            @apply text-gray-400;
          }
        }
      }
    }
</style>
