<template>
  <swiper ref="mySwiper" :options="swiperOption">
    <!-- slides -->
    <!-- <swiper-slide class="slide-1"></swiper-slide> -->
    <swiper-slide class="slide-2"></swiper-slide>

    <!-- Optional controls -->
    <!-- <div slot="pagination" class="swiper-pagination swiper-pagination-white"></div>
    <div slot="button-prev" class="swiper-button-prev swiper-button-white"></div>
    <div slot="button-next" class="swiper-button-next swiper-button-white"></div> -->
  </swiper>
</template>

<script>
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'

export default {
  name: 'FilosofiaSlider',
  components: {
    swiper,
    swiperSlide
  },
  data() {
    return {
      swiperOption: {
        spaceBetween: 30,
        speed: 4000,
        effect: 'fade',
        fadeEffect: {
          crossFade: true
        },
        // autoplay: false,
        autoplay: {
          delay: 10000,
          disableOnInteraction: false
        },
        keyboard: false
      }
    }
  },
  computed: {
    swiper() {
      return this.$refs.mySwiper.swiper
    }
  },
  mounted() {
    this.$log.debug('this is current swiper instance object', this.swiper)
  }
}
</script>

<style lang="scss" scoped>
.swiper-slide {
  @apply bg-cover bg-center;
  height: 576px;


  &.slide-2 {
    background-image:url('~@assets/filosofia_title.png')
  }
  &.slide-1 {
    background-image:url('~@assets/swiper/37_02_1493809115.jpg')
  }
}
</style>
