<template>
  <ul class="dropdown" :class="[{ 'hidden': !open }, {'arrow': arrow}]" v-click-outside="onClickOutside">
    <slot />
  </ul>
</template>

<script>
export default {
  name: 'BaseDropdown',
  props: {
    open: { // Is the dropdown open?
      type: Boolean,
      default: false
    },
    closeOnClickOutside: { // Can a user click outside to close the dropdown?
      type: Boolean,
      default: true
    },
    clickOutsideDelay: { // After how long a user can click outside to close the dropdown (ms)
      type: Number,
      default: 50
    },
    arrow: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      canClickOutside: false
    }
  },
  watch: {
    // When the dropdown opens, wait for the set delay and allow the user to click outside to close it
    open(newVal) {
      this.canClickOutside = false

      if(newVal === true) {
        setTimeout(() => { this.canClickOutside = true }, )
      }
    }
  },
  methods: {
    // When a user clicks outside, close the dropdown if the prop is true and the user can click outside (delay passed)
    onClickOutside() {
      if(this.canClickOutside && this.closeOnClickOutside)
        this.$emit('close')
    },
  }
}
</script>

<style lang="scss" scoped>
@keyframes fadeDropIn {
  from { opacity: 0; transform: translateY(-1em) perspective(600px); }
  to   { opacity: 1; transform: translateY(0) perspective(0); }
}
@keyframes fadeIn {
  from { opacity: 0; }
  to   { opacity: 1; }
}

.dropdown {
  @apply absolute border border-gray-200 bg-white text-sm z-50;
  top: 180%;
  animation: fadeDropIn .25s;
  animation-fill-mode: both;

  &.arrow:before {
    z-index: 100;
    border-style: solid;
    border-width: 6px;
    border-color: #fff transparent transparent #fff;
    width: 0;
    height: 0;
    background: transparent;
    box-shadow: -1px -1px 0 0 rgba(0,0,0,.15);
    transform: rotate(45deg);
    animation: fadeIn .25s;
    animation-fill-mode: both;
    position: absolute;
    top: -5px;
    opacity: 1;
    left: auto;
    right: .8rem;
    content: '';
    display: block;
  }

  &:not(.arrow) {
    top: calc(100% - 1px);
  }

  &.anchor-right {
    @apply right-0;
  }

  &.anchor-left {
    @apply left-0;
  }

  li {
    @apply m-0 #{!important};

    button, a {
      @apply block pl-3 pr-12 py-3 w-full mt-1 text-left border-l-4 border-transparent;
      @apply text-gray-400; // Default color

      &:hover, &.active {
        @apply relative;
        @apply border-blue-500 bg-blue-100 text-gray-600; // Default color
      }
    }

    &:first-child button {
      @apply mt-0;
    }
  }

  // The check arrow on the right of the menu item (only if dropdown has the checkArrow class)
  &.checkArrow {
    li button.active, li a.active {
      &:after {
          position: absolute;
          right: 1rem;
          top: auto;
          content: "\f00c";
          font-family: "Font Awesome 5 Pro";
          font-weight: 400;
          color: theme('colors.blue.500') // Default color
        }
    }
  }

  // Color themes
  &.orange {
    li {
      button, a {
        @apply text-gray-400;

        &:hover, &.active {
          @apply border-orange-500 bg-orange-100 text-gray-600;
        }
      }
    }

    &.checkArrow {
      li button.active, li a.active {
        &:after {
          color: theme('colors.orange.500')
        }
      }
    }
  }
}
</style>
